import React, { useEffect, useState } from 'react';

import styled from '@emotion/styled';
import { useParams } from 'react-router';

import TextHeader from '../components/TextHeader';
import {
  StyledServiceWrapper,
  TopContents,
  BottomContainer,
  BottomContents,
} from '../components/SectionPage';
import PlaylistItem from '../components/PlaylistItem';
import SongRow from '../components/SongRow';

import { api } from '../utils/api';
import { IChord } from '../types';

interface IData {
  playlist_info: { id: number; name: string };
  chord_total_count: number;
  chords: Array<IChord>;
}

const PlaylistInformation = () => {
  const params = useParams<{ playlistIdx: string }>();

  const [data, setData] = useState<IData>();

  const fetchData = () => {
    api
      .get('/playlist/detail', {
        params: { playlist_id: params?.playlistIdx },
      })
      .then(({ data }) => setData(data.data));
  };

  useEffect(fetchData, [params]);

  return (
    <StyledServiceWrapper>
      <TopContents>
        <TextHeader>PLAYLIST</TextHeader>
      </TopContents>
      <BottomContainer>
        <StyledBottomContents>
          {data && (
            <>
              <PlaylistItem
                id={parseInt(params?.playlistIdx)}
                songs={data?.chord_total_count}
                name={data?.playlist_info.name}
                selected
              />
              <SongList>
                {data?.chords.map((chord) => (
                  <SongRow
                    key={`song-row-${chord.music_id}`}
                    id={chord.music_id}
                    deleteId={chord.id}
                    title={chord.title}
                    artist={chord.singer}
                    image={chord.image_url}
                    isPlaylist={true}
                    onUpdate={() => setTimeout(fetchData, 300)}
                  />
                ))}
              </SongList>
            </>
          )}
        </StyledBottomContents>
      </BottomContainer>
    </StyledServiceWrapper>
  );
};

export default PlaylistInformation;

const StyledBottomContents = styled(BottomContents)`
  padding-top: 80px;

  @media screen and (max-width: 960px) {
    padding-top: 49px;
  }

  @media screen and (max-width: 360px) {
    padding-top: 30px;
  }
`;

const SongList = styled.div`
  margin-top: 50px;

  @media screen and (max-width: 500px) {
    margin-top: 30px;
  }
`;
