import React, { useState, useEffect } from 'react';
import styled from '@emotion/styled';
import css from '@emotion/css';
import { useHistory } from 'react-router';

import Modal from './Modal';
import { PlaylistWrap, PlaylistRow } from './PlaylistModal';
import Input from './Input';

import { makeNewPlaylist, getPlaylist, savePlaylist } from '../utils/playlist';
import { IPlaylist } from '../types';
import auth from '../utils/auth';
import useWindowSize from '../utils/useWindowSize';

import { api } from '../utils/api';

interface ISongRow {
  id: number;
  deleteId?: number;
  rank?: number;
  image: string | null;
  title: string;
  artist: string;
  visibleChecked?: boolean;
  checked?: boolean;
  onClick?: (value: boolean) => void;
  hoverStyleDifferent?: boolean;
  isPlaylist?: boolean;
  onUpdate?: () => void;
}

const SongRow = ({
  id,
  deleteId,
  rank,
  image,
  title,
  artist,
  visibleChecked = false,
  checked = false,
  onClick,
  hoverStyleDifferent = false,
  isPlaylist = false,
  onUpdate,
}: ISongRow) => {
  const history = useHistory();
  const { width: screenWidth = 1980 } = useWindowSize();

  const [playlists, setPlaylists] = useState<Array<IPlaylist>>([]);
  const [isOpenPlaylistModal, setIsOpenPlaylistModal] = useState(false);
  const [isNewPlaylist, setIsNewPlaylist] = useState(false);
  const [newPlaylistName, setNewPlaylistName] = useState<string>('');

  const isSmallScreen = screenWidth <= 960;

  const PlayListIcon = require(`../assets/songrow-icon-1.svg`).default;
  const CheckedIcon = require(`../assets/songrow-icon-check${
    checked ? 'ed' : ''
  }.svg`);
  const RemoveIcon = require(`../assets/icons/remove.svg`);

  const handleClick = () => history.push(`/notes/${id}`);

  const onRemoveItem = (e: React.MouseEvent) => {
    e.stopPropagation();
    api.delete(`/playlist/chord/delete?chord_ids=${deleteId}`);
    if (onUpdate) {
      onUpdate();
    }
  };

  useEffect(() => {
    if (isOpenPlaylistModal) {
      getPlaylist().then((playlists) => setPlaylists(playlists));
    }
  }, [isOpenPlaylistModal]);

  return (
    <>
      <Container
        className={checked ? 'songrow_checked' : ''}
        checked={checked}
        visibleChecked={visibleChecked}
        hoverStyleDifferent={hoverStyleDifferent}
        onClick={() => (onClick ? onClick(!checked) : handleClick())}
      >
        {rank && <Rank>{rank}</Rank>}
        {/* <ImageWrap>
          {image ? <Image src={image} /> : <DefaultCover />}
        </ImageWrap> */}
        <InfoWrap screenWidth={(isSmallScreen && screenWidth) || undefined}>
          <Title>{title}</Title>
          <Artist>{artist}</Artist>
        </InfoWrap>
        {!hoverStyleDifferent && (
          <RightWrap>
            {isSmallScreen ? (
              <>
                {visibleChecked ? (
                  <Button checked={checked}>
                    <CheckedIcon />
                  </Button>
                ) : isPlaylist ? (
                  <Button isOpacityDisabled onClick={onRemoveItem}>
                    <RemoveIcon />
                  </Button>
                ) : null}
              </>
            ) : (
              <>
                {isPlaylist ? (
                  <>
                    <Button isOpacityDisabled onClick={onRemoveItem}>
                      <RemoveIcon />
                    </Button>
                  </>
                ) : visibleChecked ? (
                  <Button checked={checked}>
                    <CheckedIcon />
                  </Button>
                ) : (
                  <>
                    <Button
                      onClick={(e: React.MouseEvent) => {
                        e.stopPropagation();
                        auth.getToken()
                          ? setIsOpenPlaylistModal(true)
                          : history.push('/playlist');
                      }}
                    >
                      <PlayListIcon />
                    </Button>
                  </>
                )}
              </>
            )}
          </RightWrap>
        )}
      </Container>
      {isOpenPlaylistModal && (
        <Modal
          open={isOpenPlaylistModal}
          onClose={() => setIsOpenPlaylistModal(false)}
          title={!isNewPlaylist ? 'PLAYLIST' : 'NEW PLAYLIST'}
          firstButtonLabel={
            !isNewPlaylist ? '새 플레이리스트 생성' : '생성하기'
          }
          onClickFirstButton={
            isNewPlaylist
              ? async () => {
                  await makeNewPlaylist(newPlaylistName);
                  const playlists = await getPlaylist();
                  setPlaylists(playlists);
                  setIsNewPlaylist(false);
                }
              : () => setIsNewPlaylist(true)
          }
          secondButtonLabel="취소"
          onClickSecondButton={() =>
            isNewPlaylist
              ? setIsNewPlaylist(false)
              : setIsOpenPlaylistModal(false)
          }
          content={
            <>
              {!isNewPlaylist ? (
                <PlaylistWrap>
                  {playlists.map((playlist) => (
                    <PlaylistRow
                      key={`playlist-${playlist.id}`}
                      id={playlist.id}
                      title={playlist.name}
                      count={playlist.total_count}
                      onClick={async (playlistId: number) => {
                        await savePlaylist(playlistId, id.toString());
                        const playlists = await getPlaylist();
                        setPlaylists(playlists);
                        setIsOpenPlaylistModal(false);
                      }}
                    />
                  ))}
                </PlaylistWrap>
              ) : (
                <StyledInput
                  icon="edit"
                  placeholder="PLAYLIST 이름을 입력해주세요"
                  value={newPlaylistName}
                  onChange={(e) => setNewPlaylistName(e.target.value)}
                />
              )}
            </>
          }
        />
      )}
    </>
  );
};

interface IContainer {
  checked?: boolean;
  visibleChecked?: boolean;
  hoverStyleDifferent?: boolean;
}

const Container = styled.div<IContainer>`
  display: flex;
  width: 100%;
  padding: 17px 43px 17px 29px;
  background-color: #ffffff;
  border: solid 1px #ffffff;
  border-radius: 5px;
  align-items: center;
  margin-bottom: 10px;
  cursor: pointer;

  @media screen and (max-width: 960px) {
    padding: 8px 15px;
  }

  &:last-child {
    margin-bottom: 0;
  }

  &:hover {
    border-color: #0a62ba;
  }

  html[color-mode='dark'] & {
    background-color: #1a1e27;
    border-color: #1a1e27;

    &:hover {
      border-color: #295396;
    }
  }

  ${({ checked, visibleChecked }) =>
    checked &&
    visibleChecked &&
    css`
      border-color: #0a62ba;

      html[color-mode='dark'] & {
        border-color: #182b4a;
        background-color: #182b4a;
      }
    `}

  ${({ hoverStyleDifferent }) =>
    hoverStyleDifferent &&
    css`
      &:hover {
        html[color-mode='dark'] & {
          border-color: #182b4a;
          background-color: #182b4a;
        }
      }
    `}
`;

